import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ToastController, App, AlertController, Button, Events } from 'ionic-angular';
import { StorageProvider } from '../../../providers/storage';
import { HttpServicesProvider } from '../../../providers/http-services';

/**
 * Generated class for the DialogChangepasswordPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-dialog-changepassword',
  templateUrl: 'dialog-changepassword.html',
})
export class DialogChangepasswordPage {
  public oldpassword:string='';
  public newpassword:string='';
  public comfirmpassword:string='';
  public userinfo={};
  public account='';
  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public storage:StorageProvider,
              public httpService:HttpServicesProvider,
              public view:ViewController,
              public toastCtrl:ToastController,
              public app:App,
              public alertCtrl:AlertController,
              public events:Events,
              ) {
  }
  ionViewWillEnter(){
    this.userinfo=this.navParams.get('userinfo');
    this.account=this.storage.get(StorageProvider.ME_LOGIN_USERINFO).account;
  }
  close(){
    this.view.dismiss();
  }
  keep(){
        let user={};
        let obj={};
        let userList=[]
        user=this.storage.get(StorageProvider.ME_LOGIN_USERINFO);
        userList=this.storage.get(StorageProvider.LOGIN_USERINFO_LIST);
        obj['id']=this.userinfo['id'];
        obj['oldPassword']=this.oldpassword;
        obj['newPassword']=this.newpassword;
        obj['newPasswordr']=this.comfirmpassword;
        // this.httpService.postform('/user/updatePassword','user',this.userinfo,res=>{
          this.httpService.updatePassword(obj,res=>{
            this.events.publish('wordChangeMessage',res.message);
            if(res.code==0){
              user['password']=this.comfirmpassword;
              this.storage.set(StorageProvider.ME_LOGIN_USERINFO,user);
              if( userList!=undefined && userList!=null){
                userList.forEach((value,index)=>{
                  if(value['account']==user['account']){
                    value['password']=this.comfirmpassword;
                  }
                });
              }
              this.storage.set(StorageProvider.LOGIN_USERINFO_LIST,userList);
              this.view.dismiss();
              this.events.publish('changeSuccess','');
              
            }
        })
      // }
  }

}



